<template>
    <div class="container">
       <form @submit.prevent="AgregarCliente()">
        <h3>Crear Usuario</h3>
        <input type="text" class="form-control my-2" placeholder="Su Nombre" 
        v-model="ClienteNuevo.nombre">
        <div>Informacion</div>
        <input type="text" class="form-control my-2" placeholder="Street" 
        v-model="ClienteNuevo.address.street">
        <input type="text" class="form-control my-2" placeholder="City"
         v-model="ClienteNuevo.address.city">
        <input type="text" class="form-control my-2" placeholder="Region" 
        v-model="ClienteNuevo.address.region">
        <input type="text" class="form-control my-2" placeholder="Postal Code" 
        v-model="ClienteNuevo.address.postalCode">
        <input type="text" class="form-control my-2" placeholder="Country" 
        v-model="ClienteNuevo.address.country">
        <input  class="form-control my-2" placeholder="Phone" 
        v-model="ClienteNuevo.address.phone">
        <input  class="form-control my-2" placeholder="Usuario" 
        v-model="ClienteNuevo.usuario">
        <input type="password" class="form-control my-2" placeholder="Contraseña" id="Oculto"
        v-model="ClienteNuevo.password">
        <input type="checkbox" @click="Mostrar">Mostrar Contraseña
       
        <br>
         <b-button class="btn-warning my-2 mx-2" type="submit">Registrarme</b-button>
        <b-button class=" my-2 " type="submit" href="/">Cancelar</b-button>
      </form>
   

    </div>
</template>

<script>

import {mapState,mapMutations, mapActions} from 'vuex';

export default {
    data(){
        return{
            ClienteNuevo:{
               usuario:'',
               nombre:'',
               password:'',
               address : {
                   street : '',
                   city : '',
                   region : '',
                   postalCode :'',
                   country :'',
                    phone : ''
                },
                ordenenPrevia:[]
           }
        }
    }//,
    //created(){
        
    //}
    ,
    methods:{
         ...mapMutations(['UsuarioIde']),
         Mostrar(){
           var x=document.getElementById("Oculto");
           if(x.type=== "password"){
              x.type="text"
           }else{
             x.type="password"
           }
        }, 
        
        AgregarCliente(){
           this.axios.post('/nuevo-cliente',this.ClienteNuevo)
           .then(res=>{    
              this.$store.commit('UsuarioIde',this.ClienteNuevo);    
              
              alert('Usted se ha registrado con exito!'+ this.ClienteNuevo.nombre +' Guarde su contraseña en un lugar seguro');
              this.$router.push('/main')
              
            })
            .catch(e=>{
               console.log(e.response);
                alert('Revise que todos los campos esten llenos')
           })
        }
    }
}
</script>