<template>
<div id="fondo">
  <div class="container">
    <div 
    class="bg-light rounded p-3" 
    v-if="Admin.userID==='1546' || Admin.userID==='1548'">
    <h2>Bienvenida {{Admin.Name}}</h2><br>
    <h1>Categorias</h1>
    </div>
    <div 
    class="bg-light rounded p-3" 
    v-else>
    <h2>Bienvenido {{Admin.Name}} </h2><br>
    <h1>Categorias</h1>
    </div>
    <div id="nav" class="bg-light rounded m-3">
      <router-link to="/admin/categorias">Categorias</router-link> |
      <router-link to="/admin/productos">Productos</router-link> |
      <router-link to="/admin/ordenes">Ordenes</router-link> |
      <router-link to="/admin/pendientes">Ordenes Pendientes</router-link> |
      <router-link to="/admin/clientes">Clientes</router-link> |
      <router-link to="/admin/users">Usuarios</router-link>
    </div>
    <router-view/>
    
     <b-button class="btn-success my-3" @click="agregar=true; editar=false">Agregar Categoria</b-button>
     <b-alert
       :show="dismissCountDown"
        dismissible
        :variant="mensaje.color"
        @dismissed="dismissCountDown=0"
        @dismiss-count-down="countDownChanged"
       >
          {{ mensaje.texto }}
      </b-alert>
      
      <form @submit.prevent="EditarCategoria(CategoriaEditar)" v-if="editar">
        <h3>Editar Categoria</h3>
        <input type="text" class="form-control my-2" placeholder="Id de categoria" 
        v-model="CategoriaEditar.categoryID">
        <input type="text" class="form-control my-2" placeholder="Decripcion" 
        v-model="CategoriaEditar.description">
        <input type="text" class="form-control my-2" placeholder="Nombre" 
        v-model="CategoriaEditar.name">
        
        <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
        <b-button class=" my-2 " type="submit" @click="editar=false">Cancelar</b-button>
      </form>

      <form @submit.prevent="Agregarcateg()" v-if="agregar">
        <h3>Agregar Nueva Categoria</h3>
        <input type="text" class="form-control my-2" placeholder="Id de categoria" v-model="cat.categoryID">
        <input type="text" class="form-control my-2" placeholder="Decripcion" v-model="cat.description">
        <input type="text" class="form-control my-2" placeholder="Nombre" v-model="cat.name">
       
        <b-button class="btn-success my-2 mx-2" type="submit">Agregar</b-button>
        <b-button class=" my-2 " type="submit" @click="agregar=false">Cancelar</b-button>
      </form>
    <div class="table-responsive">
      <table class="table bg-light">
     <thead>
      <tr>
       <th scope="col">ID de categoria</th>
       <th scope="col">Nombre</th>
       <th scope="col">Descripcion</th>
       
       <th scope="col">Accion</th>
     </tr>
    </thead>
    <tbody>
      <tr v-for="(item,index) in categorias" :key="index">
      <th scope="row" >{{item.categoryID}}</th>
      <td >{{item.name}}</td>
      <td >{{item.description}}</td>
      <td >
        <div v-if="item.categoryID<9 && item.categoryID>0">
        <img :src="require('../assets/CATEGORIAS/'+item.categoryID+'C.png')" style="height:150px;width:150px" alt="Imagen no disponible">
        </div>
        <div v-else>Imagen No Disponible</div>
      </td>
      <td >
        <!--<b-button @click="alerta()">Accion</b-button>-->
        <b-button @click="EliminarCategoria(item._id)" 
          class="btn-danger m-2">Elminar</b-button>
         <b-button @click="ActivarEdicion(item._id)" 
          class="btn-warning btn-sm">Editar</b-button> 
      </td>
    </tr>
    </tbody>
   </table>
    </div>
     
  </div>
  <div id="Mision" class="Mision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Misión</b></h2>
  <p style="font-size:17px;"><b>KEAFMART</b> tiene como mision ser
   la tienda online proveedora de productos de alta calidad y precios accesibles a nivel regional.
   
  </p>
</div>

<div id="Vision" class="Vision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Visión</b></h2>
  <p style="font-size:17px;">Convertirnos en la mayor dsitribuidora de productos de 
     las familias hondureñas. Siendo nuestro principal objetivo cuidar su bienestar ecónomico.</p>
</div>
<div style="width:100%;">    
   <div id="Contactanos" class="Contactanos"> 
      <h2 style="font-family:Dahnschrift Condensed"><b>Contáctanos</b></h2>  
      <br>
      <p style="font-size:17px;">
      ¡Para nosotros es un placer atenderle! <br> Contáctanos: 
      <a href="mailto:keafmart@gmail.com?Subject=Consulta">keafmart@gmail.com</a>
      <br>¡Gracias por su preferencia!
     </p> 
   <br><br>
  </div>
</div>
</div>
</template>

<script>
import {mapState,mapMutations, mapActions} from 'vuex';
export default {
    data(){
        return{
           categorias:[],
           mensaje: {color: '', texto: ''},
           dismissSecs: 5,
           dismissCountDown: 0,
           cat:{categoryID:'',description:'',name:''},
           editar:false,
           CategoriaEditar:{},
           agregar:false
        }
    },
    created(){
        this.Ver();
        this.ObtenerCategorias();
    },
    methods:{
        Ver(){
           if(!this.entrar){
             this.$router.push('/admin')
           }else{
             
           }
        },
        ObtenerCategorias(){
           this.axios.get('/categorias') 
             .then(res=>{
                this.categorias=res.data;
              })
             .catch(e=>{
               console.log(e.response);
           })
        },
        Agregarcateg(){
          //console.log(this.cat);
          this.axios.post('/nueva-categoria',this.cat)
           .then(res=>{
              this.categorias.push(res.data)
              this.cat.categoryID=''
              this.cat.description=''
              this.cat.name=''
              this.mensaje.color='success';
              this.mensaje.texto='Categoria Agregada';
              this.showAlert();
              this.agregar=false;
            })
            .catch(e=>{
               console.log(e.response);
               if(e.response.data.error.errors.name.message){
                 this.mensaje.texto=e.response.data.error.errors.name.message;
               }else{
                 this.mensaje.texto='Error del sistema'
               }
               this.mensaje.color='danger';
              this.showAlert();
           })
        },
        EliminarCategoria(id){
            this.axios.delete(`/categoria/${id}`)
            .then(res=>{
                const index=this.categorias.findIndex(item => item._id ===res.data._id)
                this.categorias.splice(index,1);
                this.mensaje.color='success';
                this.mensaje.texto='Categoria Eliminada';
                this.showAlert();
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        ActivarEdicion(id){
            this.editar=true;
            this.axios.get(`/categoria/${id}`)
            .then(res=>{
              this.CategoriaEditar=res.data;
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        EditarCategoria(item){
            this.axios.put(`/categoria/${item._id}`,item)
            .then(res=>{
              const index= this.categorias.findIndex(n=> n._id === res.data._id);
              this.categorias[index].categoryID=res.data.categoryID;
              this.categorias[index].name=res.data.name;
              this.categorias[index].description=res.data.description;
              this.mensaje.color='success';
                this.mensaje.texto='Categoria Editada';
                this.showAlert();
                this.editar=false;
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        },
        alerta(){
           this.mensaje.color='success';
           this.mensaje.texto='Probando alerta';
           this.showAlert();
        }
    },
     computed:{
      ...mapState(['Admin','entrar'])
    }

}
</script>
<style >
.fondo{
  background-image: url("../assets/Fondo.jpg");
 }
 
  .Contactanos{
    border-radius: 15px 15px 15px 15px;
    -moz-border-radius: 15px 15px 15px 15px;
    -webkit-border-radius: 15px 15px 15px 15px;
    border: 0px solid #000000;
    text-align:center;
    background-color:#f8f9fa;
    margin-top:20px;
    margin-bottom:20px; 
    margin-left:26%; 
    margin-right:15px;
    padding: 15px;
    width:46%;
    height: 200px;
    
}
.Vision{
   border-radius: 15px 15px 15px 15px;
  -moz-border-radius: 15px 15px 15px 15px;
  -webkit-border-radius: 15px 15px 15px 15px;
   border: 0px solid #000000;
   background-color:#f8f9fa;
   margin-top:20px;
   margin-left:5px;
   margin-right:25px;
   display:inline-block;
   width:46%;
   padding: 15px;
}
.Mision{
   border-radius: 15px 15px 15px 15px;
   -moz-border-radius: 15px 15px 15px 15px;
   -webkit-border-radius: 15px 15px 15px 15px; 
   border: 0px solid #000000;
   margin-top:20px;
   margin-left:4%;
   display:inline-block;
   width:46%;
   padding: 15px;
   background-color:#f8f9fa;
}

#nav {
  padding: 3px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}
</style>