<template>
<div>
    <div class="container">
      
       <div class="bg-light rounded p-3"><h1>Productos</h1></div>
      <div id="nav" class="bg-light rounded m-3">
      <router-link to="/admin/categorias">Categorias</router-link> |
      <router-link to="/admin/productos">Productos</router-link> |
      <router-link to="/admin/ordenes">Ordenes</router-link> |
      <router-link to="/admin/pendientes">Ordenes Pendientes</router-link> |
      <router-link to="/admin/clientes">Clientes</router-link> |
      <router-link to="/admin/users">Usuarios</router-link>
      </div>
      <router-view/>
     <b-button class="btn-success my-3" @click="agregar=true; editar=false">Agregar Producto</b-button>
       <b-alert
       :show="dismissCountDown"
        dismissible
        :variant="mensaje.color"
        @dismissed="dismissCountDown=0"
        @dismiss-count-down="countDownChanged"
       >
          {{ mensaje.texto }}
      </b-alert>
     <form @submit.prevent="EditarProducto(ProductoEditar)" v-if="editar">
        <h3>Editar Producto</h3>
        <input type="text" class="form-control my-2" placeholder="Id de categoria" 
        v-model="ProductoEditar.productID">
        <input type="text" class="form-control my-2" placeholder="Id de proveedor" 
        v-model="ProductoEditar.supplierID">
        <input type="text" class="form-control my-2" placeholder="Id de categoria" 
        v-model="ProductoEditar.categoryID">
        <input type="text" class="form-control my-2" placeholder="Cantidad por unidad" 
        v-model="ProductoEditar.quantityPerUnit">
        <input type="Number" class="form-control my-2" placeholder="Precio Unitario"
         v-model="ProductoEditar.unitPrice">
        <input type="Number" class="form-control my-2" placeholder="En inventario" 
        v-model="ProductoEditar.unitsInStock">
        <input type="Number" class="form-control my-2" placeholder="UnitsOnOrder" 
        v-model="ProductoEditar.unitsOnOrder">
        <input type="Number" class="form-control my-2" placeholder="Reorderlevel" 
        v-model="ProductoEditar.reorderLevel">
        <input type="Boolean" class="form-control my-2" placeholder="Descontinuo" 
        v-model="ProductoEditar.discontinued">
        <input type="text" class="form-control my-2" placeholder="Nombre" 
        v-model="ProductoEditar.name">
        <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
        <b-button class=" my-2 " type="submit" @click="editar=false">Cancelar</b-button>
      </form>
 
     <form @submit.prevent="AgregarProducto()" v-if="agregar">
        <h3>Agregar Nuevo Producto</h3>
        <input type="text" class="form-control my-2" placeholder="Id de producto" 
        v-model="prod.productID">
        <input type="text" class="form-control my-2" placeholder="Id de proveedor" 
        v-model="prod.supplierID">
        <input type="text" class="form-control my-2" placeholder="Id de categoria" 
        v-model="prod.categoryID">
        <input type="text" class="form-control my-2" placeholder="Cantidad por unidad" 
        v-model="prod.quantityPerUnit">
        <input type="Number" class="form-control my-2" placeholder="Precio Unitario"
         v-model="prod.unitPrice">
        <input type="Number" class="form-control my-2" placeholder="En inventario" 
        v-model="prod.unitsInStock">
        <input type="Number" class="form-control my-2" placeholder="UnitsOnOrder" 
        v-model="prod.unitsOnOrder">
        <input type="Number" class="form-control my-2" placeholder="Reorderlevel" 
        v-model="prod.reorderLevel">
        <input type="Boolean" class="form-control my-2" placeholder="Descontinuo" 
        v-model="prod.discontinued">
        <input type="text" class="form-control my-2" placeholder="Nombre" 
        v-model="prod.name">
        <b-button class="btn-success my-2 mx-2" type="submit">Agregar</b-button>
        <b-button class=" my-2 " type="submit" @click="agregar=false">Cancelar</b-button>
      </form>
     <div class="table-responsive">
      <table class="table bg-light">
     <thead>
      <tr >
       <th scope="col">ID de producto</th>
       <th scope="col">ID de categoria</th>
       <th scope="col">Nombre</th>
       <th scope="col">Precio</th>
       <th scope="col">En inventario</th>
       <th scope="col">Descontinuad</th>
       <th scope="col">Imagen del Producto</th>
       <th scope="col">Acciones</th>
     </tr>
    </thead>
    <tbody>
      <tr v-for="(item,index) in productos" :key="index">
      <th scope="row"><br><br>{{item.productID}}</th>
      <td><br><br>{{item.categoryID}}</td>
      <td><br><br>{{item.name}}</td>
      <td><br><br>{{item.unitPrice}}</td>
      <td><br><br>{{item.unitsInStock}}</td>
      <td><br><br>{{item.discontinued}}</td>
      <td >
        <div v-if="item.productID<78 && item.productID>0">
        <img :src="require('../assets/IMAGENESP/'+item.productID+'.png')" style="height:150px;width:150px" alt="Imagen no disponible">
        </div>
        <div v-else>Imagen No Disponible</div>
        
      </td>
      <td>
        <!--<b-button @click="alerta()">Accion</b-button>-->
        <b-button @click="EliminarProducto(item._id)" 
          class="btn-danger m-2">Elminar</b-button>
         <b-button @click="ActivarEdicion(item._id)" 
          class="btn-warning btn-sm">Editar</b-button> 
      </td>
    </tr>
    </tbody>
   </table>
   </div>
    </div>
    <div id="Mision" class="Mision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Misión</b></h2>
  <p style="font-size:17px;"><b>KEAFMART</b> tiene como mision ser
   la tienda online proveedora de productos de alta calidad y precios accesibles a nivel regional.
   </p>
</div>

<div id="Vision" class="Vision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Visión</b></h2>
  <p style="font-size:17px;">Convertirnos en la mayor dsitribuidora de productos de 
     las familias hondureñas. Siendo nuestro principal objetivo cuidar su bienestar ecónomico.</p>
</div>
<div style="width:100%;">    
   <div id="Contactanos" class="Contactanos"> 
      <h2 style="font-family:Dahnschrift Condensed"><b>Contáctanos</b></h2>  
      <br>
      <p style="font-size:17px;">
      ¡Para nosotros es un placer atenderle! <br> Contáctanos: 
      <a href="mailto:keafmart@gmail.com?Subject=Consulta">keafmart@gmail.com</a>
      <br>¡Gracias por su preferencia!
     </p> 
   <br><br>
  </div>
</div>
</div>
</template>

<script>
import {mapState,mapMutations, mapActions} from 'vuex';

export default {
        data(){
        return{
           productos:[],
           mensaje: {color: '', texto: ''},
           dismissSecs: 5,
           dismissCountDown: 0,
           prod:{productID :'',
		             supplierID : '',
		             categoryID :'',
		             quantityPerUnit :'',
		             unitPrice :0,
		             unitsInStock:0,
		             unitsOnOrder :0,
		             reorderLevel : 0,
		             discontinued :false,
                 name :''
                },
           editar:false,
           ProductoEditar:{},
           agregar:false     
        }
    },
    created(){
      this.Ver();
      this.ObtenerProductos();
    },
    methods:{
      Ver(){
           if(!this.entrar){
             this.$router.push('/admin')
           }else{
             
           }
        },
        ObtenerProductos(){
           this.axios.get('/productos') 
             .then(res=>{
                this.productos=res.data;
              })
             .catch(e=>{
               console.log(e.response);
           })
        },
        AgregarProducto(){
          this.axios.post('/nuevo-producto',this.prod)
           .then(res=>{
              this.productos.push(res.data)
              this.prod.productID='';
              this.prod.supplierID='';
              this.prod.categoryID='';
              this.prod.quantityPerUnit='';
              this.prod.unitPrice=0;
              this.prod.unitsInStock=0;
              this.prod.unitsOnOrder=0;
              this.prod.reorderLevel=0;
              this.prod.discontinued=false;
              this.prod.name=''
              this.mensaje.color='success';
              this.mensaje.texto='Producto Agregado';
              this.showAlert();
              this.agregar=false;
            })
            .catch(e=>{
               console.log(e.response);
                this.mensaje.texto='Campo Vacio o Error del sistema'
                this.mensaje.color='danger';
                this.showAlert();
           })
        },
        EliminarProducto(id){
            this.axios.delete(`/productos/${id}`)
            .then(res=>{
                const index=this.productos.findIndex(item => item._id ===res.data._id)
                this.productos.splice(index,1);
                this.mensaje.color='success';
                this.mensaje.texto='Producto Eliminado';
                this.showAlert();
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        ActivarEdicion(id){
            this.editar=true;
            this.axios.get(`/producto/${id}`)
            .then(res=>{
              this.ProductoEditar=res.data;
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        EditarProducto(item){
           this.axios.put(`/producto/${item._id}`,item)
            .then(res=>{
              const index= this.productos.findIndex(n=> n._id === res.data._id);
              this.productos[index].productID=res.data.productID;
              this.productos[index].supplierID=res.data.supplierID;
              this.productos[index].categoryID=res.data.categoryID;
              this.productos[index].quantityPerUnit=res.data.quantityPerUnit;
              this.productos[index].unitPrice=res.data.unitPrice;
              this.productos[index].unitsInStock=res.data.unitsInStock;
              this.productos[index].unitsOnOrder=res.data.unitsOnOrder;
              this.productos[index].reorderLevel=res.data.reorderLevel;
              this.productos[index].discontinued=res.data.discontinued;
              this.productos[index].name=res.data.name;
              this.mensaje.color='success';
                this.mensaje.texto='Producto Editada';
                this.showAlert();
                this.editar=false;
            })
            .catch(e=>{
              console.log(e.response)
            })
        },

        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        },
    },
        computed:{
          ...mapState(['entrar'])
        }
   
}
</script>
<style >
  .fondo{
  background-image: url("../assets/Fondo.jpg");
 }
#nav {
  padding: 3px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}
</style>