<template>
<div style="text-align: center; font-size: small;" >
    <div class="container" >
        
          <!--<b-form-input size="md" class="mr-sm-2 m-2 w-50" @click="alt=false; ordenEncontrada=false" placeholder="Ingrese su ID de orden" v-model="IDrequerido"></b-form-input> 
          <b-button size="md" class="my-2 my-sm-0 m-2" type="submit" @click="Buscar">Buscar</b-button>-->
        <div class="input-group mb-2">
        <input class="form-control md" @click="alt=false; ordenEncontrada=false" placeholder="Ingrese su ID de orden" v-model="IDrequerido"  >
         <div > 
             <b-button size="md" class="my-2 my-sm-0 m-2" type="submit" @click="Buscar">Buscar</b-button>
         </div>
        
       </div>
    
          <div class="table-responsive m-2" v-if="ordenEncontrada" >
            <table class="table bg-light">
              <thead>
                 <tr>
                   <th scope="col">ID de Orden</th>
                   <th scope="col">Estado de la orden</th>
                   <th scope="col">Fecha de emision</th>
                   <th scope="col">Fecha posible de Envio</th>
                   <th scope="col">Datos de la orden</th>
                 </tr>
              </thead>
            <tbody>
                 <tr v-for="(item,index) in ordenes" :key="index">
                    <th scope="row" v-if="item.orderID==IDrequerido" >{{item.orderID}}</th>
                    <th scope="row" v-if="item.orderID==IDrequerido" >
                      <div v-if="item.estado=='registrada'" style="color:red;">REGISTRADA</div>
                      <div v-if="item.estado=='en proceso'" style="color:red;" >EN PROCESO</div>
                      <div v-if="item.estado=='procesada'" style="color:red;">PROCESADA</div>
                    </th>
                    <td v-if="item.orderID==IDrequerido">{{item.orderDate}}</td>
                    <td v-if="item.orderID==IDrequerido">{{item.requiredDate}}</td>
                    <td v-if="item.orderID==IDrequerido">
                      <b-button v-if="!detalles" @click="detalles=true">Detalles</b-button>
                      <b-button v-if="detalles" @click="detalles=false">Ocultar</b-button>
                      <br><br>
                      <div v-if="detalles">
                       <tr v-for="(det,ind) in item.details" :key="ind">
                          <td><b>Producto {{ind+1}}</b></td>  
                          <td>
                              <tr v-for="(element,it) in productos" :key="it" >
                              <td v-if="element.productID==det.productID">
                                  <b>Nombre del Producto: </b>{{element.name}}
                              </td>
                              </tr>
                          </td>
                          <td><b>Precio Unitario: </b>{{det.unitPrice}}</td>
                          <td><b>Cantidad:</b> {{det.quantity}}<br></td>
                          <td><b>Descuento: </b>{{(det.discount)*100}}%</td>
                          
                       </tr>  
                       </div>                 
                    </td>
                </tr>
    </tbody>
   </table>
    </div>
    <div v-if="alt" class="bg-light p-3 m-2 rounded" style="width:fit-content;"> Ha ingresado un ID de orden erroneo, favor verifique su entrada </div>
    
    <div class="bg-light p-3 m-2 rounded" style="width:fit-content;font-size: small;">
      Informacion sobre el estado de la orden<br>
      <ul>
          <li><b>Registrada</b> aun no se ha empezado a sacar el pedido</li>
          <li><b>En Proceso</b> se estan sacando tu pedido </li>
          <li><b>Procesada</b> tu orden fue enviada</li>
      </ul>
      Si tienes problemas con tu orden, ve a nuestra seccion <a href="#Contactanos">Contactanos</a><br>
      ¡Gracias por preferir KEAFMART!<br>
     </div>
</div>
<div>
  <div id="Mision" class="Mision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Misión</b></h2>
  <p style="font-size:17px;"><b>KEAFMART</b> tiene como mision ser
   la tienda online proveedora de productos de alta calidad y precios accesibles a nivel regional.
    </p>
</div>

<div id="Vision" class="Vision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Visión</b></h2>
  <p style="font-size:17px;">Convertirnos en la mayor dsitribuidora de productos de 
     las familias hondureñas. Siendo nuestro principal objetivo cuidar su bienestar ecónomico.</p>
</div>
<div style="width:100%;">    
   <div id="Contactanos" class="Contactanos"> 
      <h2 style="font-family:Dahnschrift Condensed"><b>Contáctanos</b></h2>  
      <br>
      <p style="font-size:17px;">
      ¡Para nosotros es un placer atenderle! <br> Contáctanos: 
      <a href="mailto:keafmart@gmail.com?Subject=Consulta">keafmart@gmail.com</a>
      <br>¡Gracias por su preferencia!
     </p>   
   <br><br>
  </div>
</div>
</div>
</div>   

</template>

<script>
export default {
    data(){
        return{
          detalles:false,
            ordenes:[],
            IDrequerido:'',
            productos:[],
            ordenEncontrada:false,
            alt:false
        }
    },
    created(){
        this.ObtenerOrdenes();
        this.ObtenerProductos();
    },
    methods:{
        ObtenerOrdenes(){
           this.axios.get('/ordenes') 
             .then(res=>{
                this.ordenes=res.data;
              })
             .catch(e=>{
               console.log(e.response);
           })
        },
        ObtenerProductos(){
           this.axios.get('/productos') 
             .then(res=>{
                this.productos=res.data;
              })
             .catch(e=>{
               console.log(e.response);
           })
        },
        Buscar(){
            this.ordenEncontrada=false;
            for(var item in this.ordenes){
                if(this.ordenes[item].orderID==this.IDrequerido){
                    this.ordenEncontrada=true;
                    this.alt=false;
                }
            }
            if(!this.ordenEncontrada){
                this.alt=true;
                this.ordenEncontrada=false;
            }
        }
    }
}
</script>