<template>
<div id="fondo">
    <div style="text-align: center;">
      <div class="bg-light rounded p-3 container"> <h1>Ordenes</h1></div>
       <div id="nav" class="bg-light rounded m-3 mx-auto container">
      <router-link to="/admin/categorias">Categorias</router-link> |
      <router-link to="/admin/productos">Productos</router-link> |
      <router-link to="/admin/ordenes">Ordenes</router-link> |
      <router-link to="/admin/pendientes">Ordenes Pendientes</router-link> |
      <router-link to="/admin/clientes">Clientes</router-link> |
      <router-link to="/admin/users">Usuarios</router-link>
      </div>
      <router-view/>
    
       <b-button class="btn-success my-3" @click="agregar=true; editar=false">Agregar Orden</b-button>
       <b-alert
       :show="dismissCountDown"
        dismissible
        :variant="mensaje.color"
        @dismissed="dismissCountDown=0"
        @dismiss-count-down="countDownChanged"
       >
          {{ mensaje.texto }}
       </b-alert>
       <div class="container">
       <form @submit.prevent="EditarOrden(OrdenEditar)" v-if="editar">
        <h3>Editar Orden</h3>
        <input type="text" class="form-control my-2" placeholder="OrderID" 
        v-model="OrdenEditar.orderID">
        <input type="text" class="form-control my-2" placeholder="customerID" 
        v-model="OrdenEditar.customerID">
        <input type="text" class="form-control my-2" placeholder="employeeID" 
        v-model="OrdenEditar.employeeID">
        <input type="date" class="form-control my-2" placeholder="OrderDate" 
        v-model="OrdenEditar.orderDate">
        <input type="date" class="form-control my-2" placeholder="requiredDate" 
        v-model="OrdenEditar.requiredDate">
        <input type="date" class="form-control my-2" placeholder="shippedDate"
         v-model="OrdenEditar.shippedDate">
        <input type="text" class="form-control my-2" placeholder="schipVia" 
        v-model="OrdenEditar.shipVia">
        <input type="text" class="form-control my-2" placeholder="freight" 
        v-model="OrdenEditar.freight">
        <input type="text" class="form-control my-2" placeholder="schipName" 
        v-model="OrdenEditar.shipName">
        <div>shipAddress</div>
        <input  class="form-control my-2" placeholder="street" 
        v-model="OrdenEditar.shipAddress.street">
        <input type="text" class="form-control my-2" placeholder="city" 
        v-model="OrdenEditar.shipAddress.city">
        <input type="text" class="form-control my-2" placeholder="Region" 
        v-model="OrdenEditar.shipAddress.region">
        <input type="text" class="form-control my-2" placeholder="Postal Code" 
        v-model="OrdenEditar.shipAddress.postalCode">
        <input type="text" class="form-control my-2" placeholder="Country" 
        v-model="OrdenEditar.shipAddress.country">
        <div>
          <b-form-select v-model="OrdenEditar.estado" class="mb-3">
             <b-form-select-option :value="null">Selecione el estado</b-form-select-option>
             <b-form-select-option value="registrada">Registrada</b-form-select-option>
             <b-form-select-option value="en proceso">En Proceso</b-form-select-option>
             <b-form-select-option value="procesada">Procesada(enviada)</b-form-select-option>
          </b-form-select>
        </div>
        <div>Details</div>
        <input type="text" class="form-control my-2" placeholder="Id de producto" 
        v-model="OrdenEditar.details[0].productID">
        <input type="text" class="form-control my-2" placeholder="UnitPrice" 
        v-model="OrdenEditar.details[0].unitPrice">
        <input type="text" class="form-control my-2" placeholder="Cantidad"
         v-model="OrdenEditar.details[0].quantity">
        <input type="text" class="form-control my-2" placeholder="Descuento"
         v-model="OrdenEditar.details[0].discount">
         <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
        <b-button class=" my-2 " type="submit" @click="editar=false">Cancelar</b-button>
      </form>
      
       <form @submit.prevent="AgregarOrden()" v-if="agregar">
        <h3>Agregar Nueva Orden Manualmente</h3>
        <input type="text" class="form-control my-2" placeholder="OrderID" 
        v-model="orden.orderID">
        <input type="text" class="form-control my-2" placeholder="customerID" 
        v-model="orden.customerID">
        <input type="text" class="form-control my-2" placeholder="employeeID" 
        v-model="orden.employeeID">
        <div class="input-group mb-2">
        <div class="input-group-prepend"> 
             <span class="input-group-text" id="basic-addon1">Order Date </span> 
        </div>
        <input type="date" class="form-control" 
        placeholder="OrderDate" 
        aria-label="OrderDate" 
        v-model="orden.orderDate">
        </div>
        <div class="input-group mb-2">
        <div class="input-group-prepend"> 
             <span class="input-group-text" id="basic-addon1">Required Date </span> 
        </div>
        <input type="date" class="form-control" 
        placeholder="requiredDate"
        aria-label="requiredDate" 
        v-model="orden.requiredDate">
        </div>
        <div class="input-group mb-2">
        <div class="input-group-prepend"> 
             <span class="input-group-text" id="basic-addon1">shipped Date </span> 
        </div>
        <input type="date" class="form-control" placeholder="shippedDate"
         v-model="orden.shippedDate">
         </div>
         <div class="input-group mb-2">
        <div class="input-group-prepend"> 
             <span class="input-group-text" id="basic-addon1">shipVia</span> 
        </div>
        <input type="text" class="form-control " 
        placeholder="schipVia" 
        v-model="orden.shipVia">
        </div>
        <div class="input-group mb-2">
        <div class="input-group-prepend"> 
             <span class="input-group-text" id="basic-addon1">shipped Date </span> 
        </div>
        <input type="text" class="form-control" placeholder="freight" 
        v-model="orden.freight">
        </div>
        <input type="text" class="form-control my-2" placeholder="schipName" 
        v-model="orden.shipName">
        <div>shipAddress</div>
        <input type="text" class="form-control my-2" placeholder="street" 
        v-model="orden.shipAddress.street">
        <input type="text" class="form-control my-2" placeholder="city" 
        v-model="orden.shipAddress.city">
        <input type="text" class="form-control my-2" placeholder="Region" 
        v-model="orden.shipAddress.region">
        <input type="text" class="form-control my-2" placeholder="Postal Code" 
        v-model="orden.shipAddress.postalCode">
        <input type="text" class="form-control my-2" placeholder="Country" 
        v-model="orden.shipAddress.country">
        <div>
          <b-form-select v-model="orden.estado" class="mb-3">
             <b-form-select-option :value="''">Selecione el estado</b-form-select-option>
             <b-form-select-option value="registrada">Registrada</b-form-select-option>
             <b-form-select-option value="en proceso">En Proceso</b-form-select-option>
             <b-form-select-option value="procesada">Procesada(enviada)</b-form-select-option>
          </b-form-select>
        </div>
        <div>Details</div>
        <input type="text" class="form-control my-2" placeholder="Id de producto" 
        v-model="orden.details[0].productID">
        <input type="text" class="form-control my-2" placeholder="UnitPrice" 
        v-model="orden.details[0].unitPrice">
        <input type="text" class="form-control my-2" placeholder="Cantidad"
         v-model="orden.details[0].quantity">
        <input type="text" class="form-control my-2" placeholder="Descuento"
         v-model="orden.details[0].discount">
        <b-button class="btn-success my-2 mx-2 " type="submit">Agregar</b-button>
        <b-button class=" my-2 " type="submit" @click="agregar=false">Cancelar</b-button>
      </form>
      
      <div class="table-responsive">
    <table class="table bg-light">
     <thead>
      <tr >
       <th scope="col">ID de orden</th>
       <th scope="col">Fecha de emision</th>
       <th scope="col">Fecha de envio</th>
       <th scope="col">Direccion de envio</th>
       <th scope="col">Estado de la orden</th>
       <th scope="col">Datos de la orden</th>
       <th scope="col">Acciones</th>
     </tr>
    </thead>
    <tbody>
      <tr v-for="(item,index) in ordenes" :key="index">
      <th scope="row">{{item.orderID}}</th>
      <td>{{item.orderDate}}</td>
      <td>{{item.requiredDate}}</td>
      <td> <b>Calle: </b>{{item.shipAddress.street}}<br>
          <b>Ciudad: </b>{{item.shipAddress.city}}<br>
          <b>Region: </b>{{item.shipAddress.region}}<br>
          <b>Cod. Postal:</b> {{item.shipAddress.postalCode}}<br>
          <b>Pais: </b>{{item.shipAddress.country}}
      </td>
      <td>{{item.estado}}</td>
      <td>
        <tr v-for="(det,ind) in item.details" :key="ind">
          <td><b>Producto {{ind+1}}</b></td>
          <td><b>Id del producto:</b> {{det.productID}}</td>
          <td><b>Precio Unitario: </b>{{det.unitPrice}}</td>
          <td><b>Cantidad:</b> {{det.quantity}}<br></td>
          <td><b>Descuento: </b>{{(det.discount)*100}}%</td>
        </tr>
      </td>
      
      <td>
        <b-button @click="EliminarOrden(item._id)" 
          class="btn-danger m-2">Elminar</b-button>
         <b-button @click="ActivarEdicion(item._id)" 
          class="btn-warning btn-sm">Editar</b-button>
      </td>
    </tr>
    </tbody>
   </table>
   </div>
   </div>
    </div>
    <div id="Mision" class="Mision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Misión</b></h2>
  <p style="font-size:17px;"><b>KEAFMART</b> tiene como mision ser
   la tienda online proveedora de productos de alta calidad y precios accesibles a nivel regional.
     </p>
</div>

<div id="Vision" class="Vision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Visión</b></h2>
  <p style="font-size:17px;">Convertirnos en la mayor dsitribuidora de productos de 
     las familias hondureñas. Siendo nuestro principal objetivo cuidar su bienestar ecónomico.</p>
</div>
<div style="width:100%;">    
   <div id="Contactanos" class="Contactanos"> 
      <h2 style="font-family:Dahnschrift Condensed"><b>Contáctanos</b></h2>  
      <br>
      <p style="font-size:17px;">
      ¡Para nosotros es un placer atenderle! <br> Contáctanos: 
      <a href="mailto:keafmart@gmail.com?Subject=Consulta">keafmart@gmail.com</a>
      <br>¡Gracias por su preferencia!
     </p> 
   <br><br>
  </div>
</div>
</div>
</template>

<script>
import {mapState,mapMutations, mapActions} from 'vuex';

export default {
        data(){
        return{

           ordenes:[],
           mensaje: {color: '', texto: ''},
           dismissSecs: 5,
           dismissCountDown: 0,
           agregar:false,
           orden:{
              orderID : '',
	            customerID :'',
	            employeeID : '',
              orderDate :'',
	            requiredDate :'',
	            shippedDate :'',
	            shipVia :0,
	            freight :0,
              shipName :'',
              estado:'',
              shipAddress : {
                   street : '',
                   city :'',
                   region :'',
                   postalCode :'',
                   country : ''
                   },
              details : [
                    {
                    productID :'',
                    unitPrice : '',
                    quantity : 0,
                    discount : 0
                   }
              ]        
           },
           editar:false,
           OrdenEditar:{
              orderID : '',
	            customerID :'',
	            employeeID : '',
	            orderDate :Date.now(),
	            requiredDate :Date.now(),
	            shippedDate : '',
	            shipVia :0,
	            freight :0,
              shipName :'',
              estado:'',
              shipAddress : {
                   street : '',
                   city :'',
                   region :'',
                   postalCode :'',
                   country : ''
                   },
              details : [
                    {
                    productID :'',
                    unitPrice : '',
                    quantity : 0,
                    discount : 0
                   }
              ]}
        }
    },
    created(){
      this.Ver();
        this.ObtenerOrdenes();
    },
    methods:{
      Ver(){
           if(!this.entrar){
             this.$router.push('/admin')
           }else{
            }
        },
        ObtenerOrdenes(){
           this.axios.get('/ordenes') 
             .then(res=>{
                this.ordenes=res.data;
              })
             .catch(e=>{
               console.log(e.response);
           })
        },
        AgregarOrden(){
           this.axios.post('/nueva-orden',this.orden)
           .then(res=>{
              this.ordenes.push(res.data)
              this.orden.orderID='';
              this.orden.customerID='';
              this.orden.employeeID='';
              this.orden.orderDate='';
              this.orden.requiredDate='';
              this.orden.shippedDate='';
              this.orden.shipVia=0;
              this.orden.freight=0;
              this.orden.shipName='';
              this.orden.shipAddress.street='';
              this.orden.shipAddress.city='';
              this.orden.shipAddress.region='';
              this.orden.shipAddress.postalCode='';
              this.orden.shipAddress.country='';
              this.orden.details[0].productID='';
              this.orden.details[0].unitPrice='';
              this.orden.details[0].quantity='';
              this.orden.details[0].discount='';
              this.mensaje.color='success';
              this.mensaje.texto='Orden Agregada';
              this.showAlert();
              this.agregar=false;
            })
            .catch(e=>{
               console.log(e.response,'error');
                this.mensaje.texto='Campo Vacio o Error del sistema'
                this.mensaje.color='danger';
                this.showAlert();
           })
        },
        
        EliminarOrden(id){
            this.axios.delete(`/orden/${id}`)
            .then(res=>{
                const index=this.ordenes.findIndex(item => item._id ===res.data._id)
                this.ordenes.splice(index,1);
                this.mensaje.color='success';
                this.mensaje.texto='Orden Eliminada';
                this.showAlert();
                alert('Orden Eliminada')
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        ActivarEdicion(id){
            this.editar=true;
            this.axios.get(`/orden/${id}`)
            .then(res=>{
              this.OrdenEditar=res.data;
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        EditarOrden(item){
            this.axios.put(`/orden/${item._id}`,item)
            .then(res=>{
              const index= this.ordenes.findIndex(n=> n._id === res.data._id);
              this.ordenes[index].orderID=res.data.orderID;
              this.ordenes[index].customerID=res.data.customerID;
              this.ordenes[index].employeeID=res.data.employeeID;
              this.ordenes[index].orderDate=res.data.orderDate;
              this.ordenes[index].requiredDate=res.data.requiredDate;
              this.ordenes[index].shippedDate=res.data.shippedDate;
              this.ordenes[index].shipVia=res.data.shipVia;
              this.ordenes[index].shipName=res.data.shipName;
              this.ordenes[index].freight=res.data.freight;
              this.ordenes[index].estado=res.data.estado;
              this.ordenes[index].shipAddress.street=res.data.shipAddress.street;
              this.ordenes[index].shipAddress.city=res.data.shipAddress.city;
              this.ordenes[index].shipAddress.region=res.data.shipAddress.region;
              this.ordenes[index].shipAddress.postalCode=res.data.shipAddress.postalCode;
              this.ordenes[index].shipAddress.country=res.data.shipAddress.country;
              this.ordenes[index].details[0].productID=res.data.details[0].productID;
              this.ordenes[index].details[0].unitPrice=res.data.details[0].unitPrice;
              this.ordenes[index].details[0].quantity=res.data.details[0].quantity;
              this.ordenes[index].details[0].discount=res.data.details[0].discount;
              this.mensaje.color='success';
                this.mensaje.texto='Orden Editada';
                this.showAlert();
                alert('Orden Editada')
                this.editar=false;
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        }
    },
     computed:{
      ...mapState(['Admin','entrar'])
    }
}
</script>

<style >
.fondo{
  background-image: url("../assets/Fondo.jpg");
 }
 
  .Contactanos{
    border-radius: 15px 15px 15px 15px;
    -moz-border-radius: 15px 15px 15px 15px;
    -webkit-border-radius: 15px 15px 15px 15px;
    border: 0px solid #000000;
    text-align:center;
    background-color:#f8f9fa;
    margin-top:20px;
    margin-bottom:20px; 
    margin-left:26%; 
    margin-right:15px;
    padding: 15px;
    width:46%;
    height: 200px;
    
}
.Vision{
   border-radius: 15px 15px 15px 15px;
  -moz-border-radius: 15px 15px 15px 15px;
  -webkit-border-radius: 15px 15px 15px 15px;
   border: 0px solid #000000;
   background-color:#f8f9fa;
   margin-top:20px;
   margin-left:5px;
   margin-right:25px;
   display:inline-block;
   width:46%;
   padding: 15px;
}
.Mision{
   border-radius: 15px 15px 15px 15px;
   -moz-border-radius: 15px 15px 15px 15px;
   -webkit-border-radius: 15px 15px 15px 15px; 
   border: 0px solid #000000;
   margin-top:20px;
   margin-left:4%;
   display:inline-block;
   width:46%;
   padding: 15px;
   background-color:#f8f9fa;
}

#nav {
  padding: 3px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
