<template>
<div>
  <div class="container">
    <div class="bg-light rounded p-3" >
    <h1>Usuarios</h1>
    </div>
    
    <div id="nav" class="bg-light rounded m-3">
      <router-link to="/admin/categorias">Categorias</router-link> |
      <router-link to="/admin/productos">Productos</router-link> |
      <router-link to="/admin/ordenes">Ordenes</router-link> |
      <router-link to="/admin/pendientes">Ordenes Pendientes</router-link> |
      <router-link to="/admin/clientes">Clientes</router-link> |
      <router-link to="/admin/users">Usuarios</router-link>
    </div>
    <router-view/>
     <b-alert
       :show="dismissCountDown"
        dismissible
        :variant="mensaje.color"
        @dismissed="dismissCountDown=0"
        @dismiss-count-down="countDownChanged"
       >
          {{ mensaje.texto }}
      </b-alert>
      
      <form @submit.prevent="EditarAdministrador(AdministradorEditar)" v-if="editar">
        <h3>Editar Usuario</h3>
        <input type="text" class="form-control my-2" placeholder="Nombre" 
        v-model="AdministradorEditar.Name">
        <input type="text" class="form-control my-2" placeholder="Login" 
        v-model="AdministradorEditar.Login">
        <input type="password" class="form-control my-2" placeholder="Old Password or New Password" 
        v-model="newPassword" id="Oculto">
        <div class="bg-light rounded m-4"><input type="checkbox" @click="Mostrar">Mostrar Contraseña</div>
        <input type="text" class="form-control my-2" placeholder="email" 
        v-model="AdministradorEditar.email">
        <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
        <b-button class=" my-2 " type="submit" @click="editar=false">Cancelar</b-button>
      </form>
      <b-button class="btn-success m-2" @click="agregar=true">Agregar Nuevo Administrador</b-button>
      <form @submit.prevent="AgregarUsuario()" v-if="agregar">
        <h3>Agregar Nuevo Producto</h3>
        <input type="text" class="form-control my-2" placeholder="Nombre" 
        v-model="AdministradorNuevo.Name">
        <input type="text" class="form-control my-2" placeholder="Login" 
        v-model="AdministradorNuevo.Login">
        <input type="password" class="form-control my-2" placeholder="Password" 
        v-model="AdministradorNuevo.Password" id="Oculto">
        <div class="bg-light rounded m-4"><input type="checkbox" @click="Mostrar">Mostrar Contraseña</div>
        <input type="text" class="form-control my-2" placeholder="email" 
        v-model="AdministradorNuevo.email">
        <b-button class="btn-success my-2 mx-2" type="submit">Agregar</b-button>
        <b-button class=" my-2 " type="submit" @click="agregar=false">Cancelar</b-button>
      </form>
    <div class="table-responsive">
     <table class="table bg-light">
     <thead>
      <tr>
       <th scope="col">ID de usuario</th>
       <th scope="col">Imagen</th>
       <th scope="col">Nombre</th>
       <th scope="col">email</th>
       <th scope="col">Accion</th>
     </tr>
    </thead>
    <tbody>
      <tr v-for="(item,index) in administradores" :key="index">
      <th scope="row" ><br><br><br>{{item.userID}}</th>
      <td>
        <div v-if="item.userID<1550 && item.userID>1545">
        <img :src="require('../assets/USUARIOS/'+item.Login+'.png')" style="height:200px;width:200px" alt="Imagen no disponible">
        </div>
        <div v-else>
          <img :src="require('../assets/USUARIOS/person_box.png')" style="height:200px;width:200px" alt="Imagen no disponible">
          
        </div>
  
      </td>
      <td ><br><br><br>{{item.Name}}</td>
      <td ><br><br><br>{{item.email}}</td>
      <td >
        <!--<b-button @click="alerta()">Accion</b-button>-->
         <b-button  v-if="Admin._id===item._id" @click="ActivarEdicion(item._id)" 
          class="btn-warning btn-sm">Editar</b-button> 
      </td>
    </tr>
    </tbody>
   </table>
   </div>
  </div>
  <div id="Mision" class="Mision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Misión</b></h2>
  <p style="font-size:17px;"><b>KEAFMART</b> tiene como mision ser
   la tienda online proveedora de productos de alta calidad y precios accesibles a nivel regional.
    </p>
</div>

<div id="Vision" class="Vision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Visión</b></h2>
  <p style="font-size:17px;">Convertirnos en la mayor dsitribuidora de productos de 
     las familias hondureñas. Siendo nuestro principal objetivo cuidar su bienestar ecónomico.</p>
</div>
<div style="width:100%;">    
   <div id="Contactanos" class="Contactanos"> 
      <h2 style="font-family:Dahnschrift Condensed"><b>Contáctanos</b></h2>  
      <br>
       <p style="font-size:17px;">
      ¡Para nosotros es un placer atenderle! <br> Contáctanos: 
      <a href="mailto:keafmart@gmail.com?Subject=Consulta">keafmart@gmail.com</a>
      <br>¡Gracias por su preferencia!
     </p>    
   <br><br>
  </div>
</div>
</div>
</template>

<script>
import {mapState,mapMutations, mapActions} from 'vuex';
export default {
    data(){
        return{
          agregar:false,
           administradores:[],
           mensaje: {color: '', texto: ''},
           dismissSecs: 5,
           dismissCountDown: 0,
           AdministradorEditar:{userID:'',Login:'',Password:'',Name:'',email:''},
           editar:false,
           newPassword:'',
           AdministradorNuevo:{userID:'',Login:'',Password:'',Name:'',email:''}
        }
    },
    created(){
        this.Ver();
        this.ObtenerUsuarios();
    },
    methods:{
      Ver(){
           if(!this.entrar){
             this.$router.push('/admin')
           }else{
             console.log('Esta registrado')
           }
        },
        Mostrar(){
           var x=document.getElementById("Oculto");
           if(x.type=== "password"){
              x.type="text"
           }else{
             x.type="password"
           }
        },
        ObtenerUsuarios(){
           this.axios.get('/admins') 
             .then(res=>{
                this.administradores=res.data;
              })
             .catch(e=>{
               console.log(e.response);
           })
        },
        ActivarEdicion(id){
            this.editar=true;
            this.axios.get(`/admin/${id}`)
            .then(res=>{
              this.AdministradorEditar=res.data;
              //this.AdministradorEditar.Password='Ingrese su nueva contraseña';
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        EditarAdministrador(item){
          item.Password=this.newPassword;
          console.log(item.Password,'Password');
            this.axios.put(`/admin/${item._id}`,item)
            .then(res=>{
              const index= this.administradores.findIndex(n=> n._id === res.data._id);
              this.administradores[index].userID=res.data.userID;
              this.administradores[index].Name=res.data.Name;
              this.administradores[index].Login=res.data.Login;
              this.administradores[index].Password=res.data.Password;
              this.administradores[index].email=res.data.email;
              this.mensaje.color='success';
                this.mensaje.texto='Usuario Editado';
                this.showAlert();
                this.editar=false;
            })
            .catch(e=>{
              console.log(e.response)
            })
        },AgregarUsuario(){
          var Ident=0
          for(var item in this.administradores){
            var user=parseInt(this.administradores[item].userID)
            console.log(this.administradores[item].userID.user)
             if(user>Ident){
               Ident=user
               console.log(Ident)
             }  
          }
         this.AdministradorNuevo.userID=(Ident+1).toString();
          this.axios.post('/nuevo-administrador',this.AdministradorNuevo)
           .then(res=>{
              this.administradores.push(res.data)
              this.AdministradorNuevo.Login='';
              this.AdministradorNuevo.Password='';
              this.AdministradorNuevo.userID='';
              this.AdministradorNuevo.Name='';
              this.AdministradorNuevo.email='';
              this.mensaje.color='success';
              this.mensaje.texto='Usuario Agregado';
              this.showAlert();
              this.agregar=false;
            })
            .catch(e=>{
               console.log(e.response);
                this.mensaje.texto='Campo Vacio o Error del sistema'
                this.mensaje.color='danger';
                this.showAlert();
           })
        },
        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        }
        
    },
     computed:{
      ...mapState(['Admin','entrar'])
    }

}
</script>
<style >
  
#nav {
  padding: 3px;
  
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}
</style>