<template>
<div id="fondo">
  <div class="container">
    <b-alert
       :show="dismissCountDown"
        dismissible
        :variant="mensaje.color"
        @dismissed="dismissCountDown=0"
        @dismiss-count-down="countDownChanged"
       >
          {{ mensaje.texto }}
      </b-alert>
     <div class="table-responsive">
    <table class="table bg-light">
     <thead>
      <tr>
       <th>Id de categoria</th>
       <th scope="col">Nombre del Producto</th>
       <th scope="col">Precio por unidad</th>
       <th scope="col">Cantidad por unidad</th>
       <th scope="col">Producto</th>
       
       <th scope="col">En inventario</th>
       <th scope="col">Acciones</th>
     </tr>
    </thead>
    <tbody>
      <tr v-for="(item,index) in productos" :key="index">
      <td v-if="item.unitsInStock!=0 && item.categoryID==categoria"><br><br>{{item.categoryID}}</td>  
      <td v-if="item.unitsInStock!=0 && item.categoryID==categoria"><br><br>{{item.name}}</td>
      <td v-if="item.unitsInStock!=0 && item.categoryID==categoria"><br><br>L. {{item.unitPrice}}</td>
      <td v-if="item.unitsInStock!=0 && item.categoryID==categoria"><br><br> {{item.quantityPerUnit}}</td>
      <td v-if="item.unitsInStock!=0 && item.categoryID==categoria" >
        <div v-if="item.unitsInStock!=0 && item.categoryID==categoria">
        <img :src="require('../assets/IMAGENESP/'+item.productID+'.png')" style="height:150px;width:150px" alt="Imagen no disponible">
        </div>
        <div v-else><br><br>Imagen No Disponible</div>
  
      </td>

      <td v-if="item.unitsInStock!=0 && item.categoryID==categoria"><br><br>{{item.unitsInStock}}</td>
      <td v-if="item.unitsInStock!=0 && item.categoryID==categoria">
        <!--<b-button @click="alerta()">Accion</b-button>-->
         <br><br>
          <input type="Number" placeholder="Cantidad" class="form-control my-2 form-control-sm" style="width:60px;" v-model="cantidad[index]">
        
        <div v-if="cantidad>item.unitsInStock">La cantidad deseada Sobrepasa la cantidad que tenemos enexistencia</div>
        
         <b-button class="btn-warning btn-sm" @click="compra(item._id,index)">Comprar</b-button> 
      </td>
    </tr>
    </tbody>
   </table>
   </div>
   </div>
   <div id="Mision" class="Mision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Misión</b></h2>
  <p style="font-size:17px;"><b>KEAFMART</b> tiene como mision ser
   la tienda online proveedora de productos de alta calidad y precios accesibles a nivel regional.
    
  </p>
</div>

<div id="Vision" class="Vision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Visión</b></h2>
  <p style="font-size:17px;">Convertirnos en la mayor dsitribuidora de productos de 
     las familias hondureñas. Siendo nuestro principal objetivo cuidar su bienestar ecónomico.</p>
</div>
<div style="width:100%;">    
   <div id="Contactanos" class="Contactanos"> 
      <h2 style="font-family:Dahnschrift Condensed"><b>Contáctanos</b></h2>  
      <br>
      <p style="font-size:17px;">
      ¡Para nosotros es un placer atenderle! <br> Contáctanos: 
      <a href="mailto:keafmart@gmail.com?Subject=Consulta">keafmart@gmail.com</a>
      <br>¡Gracias por su preferencia!
     </p> 
   <br><br>
  </div>
</div>
  </div>
</template>


<script>
import {mapState,mapMutations, mapActions} from 'vuex';
export default {
        data(){
        return{
          categorias:[],
           cantidad:[],
           productos:[],
           prodAdd:{},
           mensaje: {color: '', texto: ''},
           dismissSecs: 2,
           dismissCountDown: 0
                
        }
    },
    computed:{
      ...mapState(['estaCompra','total','categoria','Usuario'])
    },
    created(){
        this.ObtenerProductos();
    },
    methods:{
      ...mapMutations(['AgregarAlCarrito','AgregarOrden','AggCompra']),
        ObtenerProductos(){
           this.axios.get('/productos') 
             .then(res=>{
                this.productos=res.data;
                for(var item in this.productos){
                  this.cantidad.push(1)
                }
              })
             .catch(e=>{
               
           })
        },
        compra(id,indice){
          var cate=false;
          this.axios.get(`/producto/${id}`)
            .then(res=>{
              this.prodAdd=res.data;
              
              this.$store.commit('AgregarAlCarrito',[this.prodAdd,this.cantidad[indice]]);
              this.$store.commit('AgregarOrden',[this.prodAdd,this.cantidad[indice]]);
              this.cantidad[indice]=1;
              for(var item in this.categorias){
                console.log()
                if(this.categorias[item].categoriavisitada==this.categoria){
                   cate=true;
                }
              }
              if(!cate){
                var categoriavisitada=this.prodAdd.categoryID;
                this.categorias.push({categoriavisitada});
                this.$store.commit('AggCompra',this.categorias);
              
              }
              alert(this.prodAdd.name +' Agregado al carrito');
              
            })
            .catch(e=>{
              console.log(e.response,"error")
            })
        },
        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        }
    }

}
</script>

<style>
.fondo{
  background-image: url("../assets/Fondo.jpg");
 }
 

  .Contactanos{
    border-radius: 15px 15px 15px 15px;
    -moz-border-radius: 15px 15px 15px 15px;
    -webkit-border-radius: 15px 15px 15px 15px;
    border: 0px solid #000000;
    text-align:center;
    background-color:#f8f9fa;
    margin-top:20px;
    margin-bottom:20px; 
    margin-left:26%; 
    margin-right:15px;
    padding: 15px;
    width:46%;
    height:200px;
    
}
.Vision{
   border-radius: 15px 15px 15px 15px;
  -moz-border-radius: 15px 15px 15px 15px;
  -webkit-border-radius: 15px 15px 15px 15px;
   border: 0px solid #000000;
   background-color:#f8f9fa;
   margin-top:20px;
   margin-left:5px;
   margin-right:25px;
   display:inline-block;
   width:46%;
   padding: 15px;
}
.Mision{
   border-radius: 15px 15px 15px 15px;
   -moz-border-radius: 15px 15px 15px 15px;
   -webkit-border-radius: 15px 15px 15px 15px; 
   border: 0px solid #000000;
   margin-top:20px;
   margin-left:4%;
   display:inline-block;
   width:46%;
   padding: 15px;
   background-color:#f8f9fa;
}

</style>