<template>
    <div style="text-align: center;
                margin-top: 5%;
                font-family:'Times New Roman';"
            id="fondo">
        <div class="container" 
          style="padding:30px;
                width: fit-content;
                background-color: gainsboro;
                border-radius:20px 20px 20px 20px;" >
       <div>
           <h3 style="display: inline-block;color: black;">Login:  </h3>
           <input @click="Vaciar" v-model="Registro.Login" 
           class="form-control" placeholder="Usuario" 
           @keyup.enter="Verificar">
        </div>
       <div style="margin-bottom: 30px;">
           <h3 style="display: inline-block;color: rgb(0, 0, 0);">Password:  </h3>
           <input @click="Vaciar" v-model="Registro.password" id="Oculto"
           type="password" class="form-control" placeholder="Password" 
           @keyup.enter="Verificar">
       </div>
       <input type="checkbox" @click="Mostrar">Mostrar Contraseña
        <button class="btn btn-warning btn-block" style="margin-top:20px;" @click="Verificar">Entrar</button>
       <div style="color:red;margin-top:20px;">{{Mensaje}}</div>
       </div>
        <div>¿Aún no estás registrado?   
            <router-link to="/registrarse">Registrate aquí</router-link>
        </div>
</div>
</template>


<script>

import {mapState,mapMutations, mapActions} from 'vuex';
export default {
    name:'Inicio',
    data(){
        return{
          Registro:{
              Login:'',
              password:''
            },
          Mensaje:'',
          administradores:[]
        }
    },
    
    created(){
        this.ObtenerClientes();
    },
    methods:{
        ...mapMutations(['UsuarioIde']),
        Mostrar(){
           var x=document.getElementById("Oculto");
           if(x.type=== "password"){
              x.type="text"
           }else{
             x.type="password"
           }
        }, 
        ObtenerClientes(){
           this.axios.get('/clientes') 
             .then(res=>{
                this.administradores=res.data;
              })
             .catch(e=>{
               console.log(e.response);
           })
        },
        Verificar(){
             this.Mensaje='';
             
            this.axios.post("/us", this.Registro)
              .then(res=>{
                  var aux= res.data.usuarioDB;
                  this.$store.commit('UsuarioIde',res.data.usuarioDB);
                  this.$router.push('/')
                  //console.log(res.data,'paso', aux);
              })
              .catch(err=>{
                  console.log(e.response.data.mensaje);
                  this.Mensaje='Usuario y/o contraseñas incorrectas';
              })
           
        },
        Vaciar(){
            this.Mensaje='';
            
        }
    }
}
</script>