<template>
<div id="fondo">
    <div class="container">
       <div class="bg-light rounded p-3"><h1>Usuarios</h1></div>
       <div id="nav" class="bg-light rounded m-3">
      <router-link to="/admin/categorias">Categorias</router-link> |
      <router-link to="/admin/productos">Productos</router-link> |
      <router-link to="/admin/ordenes">Ordenes</router-link> ||
      <router-link to="/admin/pendientes">Ordenes Pendientes</router-link> |
      <router-link to="/admin/clientes">Clientes</router-link> |
      <router-link to="/admin/users">Usuarios</router-link>
    </div>
    <router-view/>
       <b-button class="btn-success my-3" @click="agregar=true; editar=false">Agregar Cliente</b-button>
       <b-alert
       :show="dismissCountDown"
        dismissible
        :variant="mensaje.color"
        @dismissed="dismissCountDown=0"
        @dismiss-count-down="countDownChanged"
       >
          {{ mensaje.texto }}
       </b-alert>
       <form @submit.prevent="EditarUsuario(usuarioEditar)" v-if="editar">
        <h3>Agregar Nuevo Cliente</h3>
        <input type="text" class="form-control my-2" placeholder="ID de cliente" 
        v-model="usuarioEditar.customerID">
        <input type="text" class="form-control my-2" placeholder="companyName" 
        v-model="usuarioEditar.companyName">
        <input type="text" class="form-control my-2" placeholder="contactName" 
        v-model="usuarioEditar.contactName">
        <input type="text" class="form-control my-2" placeholder="contactTitle" 
        v-model="usuarioEditar.contactTitle">
        <div>Informacion</div>
        <input type="text" class="form-control my-2" placeholder="Street" 
        v-model="usuarioEditar.address.street">
        <input type="text" class="form-control my-2" placeholder="City"
         v-model="usuarioEditar.address.city">
        <input type="text" class="form-control my-2" placeholder="Region" 
        v-model="usuarioEditar.address.region">
        <input type="text" class="form-control my-2" placeholder="Psotal Code" 
        v-model="usuarioEditar.address.postalCode">
        <input type="text" class="form-control my-2" placeholder="Country" 
        v-model="usuarioEditar.address.country">
        <input  class="form-control my-2" placeholder="Phone" 
        v-model="usuarioEditar.address.phone">
         <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
        <b-button class=" my-2 " type="submit" @click="editar=false">Cancelar</b-button>
      </form>
   
       <form @submit.prevent="AgregarUsuario()" v-if="agregar">
        <h3>Agregar Nuevo Cliente</h3>
        <input type="text" class="form-control my-2" placeholder="ID de cliente" 
        v-model="usuario.customerID">
        <input type="text" class="form-control my-2" placeholder="companyName" 
        v-model="usuario.companyName">
        <input type="text" class="form-control my-2" placeholder="contactName" 
        v-model="usuario.contactName">
        <input type="text" class="form-control my-2" placeholder="contactTitle" 
        v-model="usuario.contactTitle">
        <div>Informacion</div>
        <input type="text" class="form-control my-2" placeholder="Street" 
        v-model="usuario.address.street">
        <input type="text" class="form-control my-2" placeholder="City"
         v-model="usuario.address.city">
        <input type="text" class="form-control my-2" placeholder="Region" 
        v-model="usuario.address.region">
        <input type="text" class="form-control my-2" placeholder="Postal Code" 
        v-model="usuario.address.postalCode">
        <input type="text" class="form-control my-2" placeholder="Country" 
        v-model="usuario.address.country">
        <input type="text" class="form-control my-2" placeholder="Phone" 
        v-model="usuario.address.phone">
        <b-button class="btn-success my-2 mx-2 " type="submit">Agregar</b-button>
        <b-button class=" my-2 " type="submit" @click="agregar=false">Cancelar</b-button>
      </form>
    <div class="table-responsive">
    <table class="table bg-light">
     <thead>
      <tr >
       <th scope="col">ID de Usuario</th>
       <th scope="col">Contact Name</th>
       <th scope="col">Informacion</th>
       <th scopte="col">Acciones</th>
     </tr>
    </thead>
    <tbody>
      <tr v-for="(item,index) in usuarios" :key="index">
      <th scope="row">{{item.customerID}}</th>
      <td>{{item.contactName}}</td>
      <td> <b>Calle: </b>{{item.address.street}}<br>
          <b>Ciudad: </b>{{item.address.city}}<br>
          <b>Region: </b>{{item.address.region}}<br>
          <b>Cod. Postal:</b> {{item.address.postalCode}}<br>
          <b>Pais: </b>{{item.address.country}}<br>
          <b>Tel.: </b>{{item.address.phone}}
      </td>
      <td>
        <b-button @click="EliminarUsuario(item._id)" 
          class="btn-danger m-2">Elminar</b-button>
         <b-button @click="ActivarEdicion(item._id)" 
          class="btn-warning btn-sm">Editar</b-button>
      </td>
    </tr>
    </tbody>
   </table>
   </div>
    </div>
    <div id="Mision" class="Mision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Misión</b></h2>
  <p style="font-size:17px;"><b>KEAFMART</b> tiene como mision ser
   la tienda online proveedora de productos de alta calidad y precios accesibles a nivel regional.
    
   </p>
</div>

<div id="Vision" class="Vision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Visión</b></h2>
  <p style="font-size:17px;">Convertirnos en la mayor dsitribuidora de productos de 
     las familias hondureñas. Siendo nuestro principal objetivo cuidar su bienestar ecónomico.</p>
</div>
<div style="width:100%;">    
   <div id="Contactanos" class="Contactanos"> 
      <h2 style="font-family:Dahnschrift Condensed"><b>Contáctanos</b></h2>  
      <br>
      <p style="font-size:17px;">
      ¡Para nosotros es un placer atenderle! <br> Contáctanos: 
      <a href="mailto:keafmart@gmail.com?Subject=Consulta">keafmart@gmail.com</a>
      <br>¡Gracias por su preferencia!
     </p> 
   <br><br>
  </div>
</div>
</div>
</template>

<script>
import {mapState,mapMutations, mapActions} from 'vuex';

export default {
        data(){
        return{
           usuarios:[],
           mensaje: {color: '', texto: ''},
           dismissSecs: 5,
           dismissCountDown: 0,
           agregar:false,
           usuario:{
                customerID:'',
                companyName:'',
                contactName:'',
                contactTitle:'',
                address : {
                    street :'',
                     city : '',
                    region : '',
                    postalCode : '',
                    country : '',
                    phone :'' 
                 }       
           },
           editar:false,
           usuarioEditar:{
                customerID:'',
                companyName:'',
                contactName:'',
                contactTitle:'',
                address : {
                    street :'',
                    city : '',
                    region : '',
                    postalCode : '',
                    country : '',
                    phone :'' 
                 }       
           }
        }
    },
    created(){
      this.Ver();
        this.ObtenerUsuarios();
    },
    methods:{
      Ver(){
           if(!this.entrar){
             this.$router.push('/admin')
           }else{
            }
        },
        ObtenerUsuarios(){
           this.axios.get('/usuarios') 
             .then(res=>{
                this.usuarios=res.data;
              })
             .catch(e=>{
               console.log(e.response);
           })
        },
        AgregarUsuario(){
           this.axios.post('/nuevo-usuario',this.usuario)
           .then(res=>{
              this.usuarios.push(res.data);

              this.usuario.customerID='';
              this.usuario.companyName='';
              this.usuario.contactName='';
              this.usuario.contactTitle='';
              this.usuario.address.street='';
              this.usuario.address.city='';
              this.usuario.address.region='';
              this.usuario.address.postalCode='';
              this.usuario.address.country='';
              this.usuario.address.phone='';
              this.mensaje.color='success';
              this.mensaje.texto='Cliente Agregado con Exito!';
              this.showAlert();
              this.agregar=false;
            })
            .catch(e=>{
               console.log(e.response);
                this.mensaje.texto='Campo Vacio o Error del sistema'
                this.mensaje.color='danger';
                this.showAlert();
           })
        },
        
        EliminarUsuario(id){
          this.axios.delete(`/usuario/${id}`)
            .then(res=>{
                const index=this.usuarios.findIndex(item => item._id ===res.data._id)
                this.usuarios.splice(index,1);
                this.mensaje.color='success';
                this.mensaje.texto='Cliente Eliminado de la Base de Datos';
                this.showAlert();
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        ActivarEdicion(id){
            this.editar=true;
            this.axios.get(`/usuario/${id}`)
            .then(res=>{
              this.usuarioEditar=res.data;
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        EditarUsuario(item){
            this.axios.put(`/usuario/${item._id}`,item)
            .then(res=>{
              const index= this.usuarios.findIndex(n=> n._id === res.data._id);
              this.usuarios[index].customerID=res.data.customerID;
              this.usuarios[index].companyName=res.data.companyName;
              this.usuarios[index].contactName=res.data.contactName;
              this.usuarios[index].contactTitle=res.data.contactTitle;
              this.usuarios[index].address.street=res.data.address.street;
              this.usuarios[index].address.city=res.data.address.city;
              this.usuarios[index].address.region=res.data.address.region;
              this.usuarios[index].address.postalCode=res.data.address.postalCode;
              this.usuarios[index].address.country=res.data.address.country;
              this.usuarios[index].address.phone=res.data.address.phone;
              this.mensaje.color='success';
                this.mensaje.texto='Informacion de Cliente Editado';
                this.showAlert();
                this.editar=false;
            })
            .catch(e=>{
              console.log(e.response)
            })
        },
        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        }
    },
        computed:{
          ...mapState(['entrar'])
        }
}
</script>

<style >

.fondo{
  background-image: url("../assets/Fondo.jpg");
 }
 
  .Contactanos{
    border-radius: 15px 15px 15px 15px;
    -moz-border-radius: 15px 15px 15px 15px;
    -webkit-border-radius: 15px 15px 15px 15px;
    border: 0px solid #000000;
    text-align:center;
    background-color:#f8f9fa;
    margin-top:20px;
    margin-bottom:20px; 
    margin-left:26%; 
    margin-right:15px;
    padding: 15px;
    width:46%;
    height: 200px;
    
}
.Vision{
   border-radius: 15px 15px 15px 15px;
  -moz-border-radius: 15px 15px 15px 15px;
  -webkit-border-radius: 15px 15px 15px 15px;
   border: 0px solid #000000;
   background-color:#f8f9fa;
   margin-top:20px;
   margin-left:5px;
   margin-right:25px;
   display:inline-block;
   width:46%;
   padding: 15px;
}
.Mision{
   border-radius: 15px 15px 15px 15px;
   -moz-border-radius: 15px 15px 15px 15px;
   -webkit-border-radius: 15px 15px 15px 15px; 
   border: 0px solid #000000;
   margin-top:20px;
   margin-left:4%;
   display:inline-block;
   width:46%;
   padding: 15px;
   background-color:#f8f9fa;
}


#nav {
  padding: 3px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

