<template>
<div id="fondo">
  <div class="container" v-if="!ordenLista">
      <b-alert
       :show="dismissCountDown"
        dismissible
        :variant="mensaje.color"
        @dismissed="dismissCountDown=0"
        @dismiss-count-down="countDownChanged"
       >
          {{ mensaje.texto }}
      </b-alert>
     
      <div class="bg-light rounded" 
      style="float:right; width:20%; margin:5px; padding:3px; "><b>Total a pagar: L. {{total}} </b></div>
      <div class="table-responsive">
    <table class="table bg-light" v-if="!Completar">
     <thead>
      <tr >
       <th scope="col">Nombre del Producto</th>
       <th scope="col">Precio</th>
       <th>Cantidad</th>
      
     </tr>
    </thead>
    <tbody>
      <tr v-for="(item,index) in estaCompra" :key="index">
      <td>{{item[0].name}}</td>
      <td>L. {{item[0].unitPrice}}</td>
      <td>
          <b-button class="btn btn-sm m-1" 
          @click="Aumentar(item[0].unitPrice); orden.details[index].quantity++; ">+</b-button>
          {{orden.details[index].quantity}}
          <b-button v-if="orden.details[index].quantity>1" 
            class="btn btn-sm m-1" 
            @click="Disminuir(item[0].unitPrice); orden.details[index].quantity--; ">-</b-button>    
      </td>
    </tr>
    </tbody>
   </table>
   </div>
   <b-button v-if="!Completar" @click="Completar=true" class="btn-warning btn-md">Completar Orden</b-button>
   <form @submit.prevent="AgregarOrden();" v-if="Completar">
        <input type="text" class="form-control my-2" 
        placeholder="street" v-model="inform.street">
        <input type="text" class="form-control my-2" 
        placeholder="city" v-model="inform.city">
        <input type="text" class="form-control my-2" 
        placeholder="Region" v-model="inform.Region">
        <input type="text" class="form-control my-2" 
        placeholder="Postal Code" v-model="inform.PostalCode">
        <input type="text" class="form-control my-2" 
        placeholder="Country" v-model="inform.Country">
        <input type="text" class="form-control my-2" 
        placeholder="Tarjeta de Credito"
        v-model="TarjetaC">
        <input type="text" class="form-control my-2" placeholder="Nombre del remitente" v-model="Nombre">
        <b-button class="btn-warning my-2 " type="submit">Ordenar</b-button>
      </form>
      
  </div>
  <div class="container" v-if="ordenLista">
       <div v-if="enviaremail">
         <div class="bg-light m-2 p-2 rounded" style="width:fit-content;" v-if="enviaremail">
      Si desea enviar copia de su ID de orden a su correo, proporcione su correo aqui</div>
    
       <form id="app" @submit="enviar" method="post" novalidate="true" >
         <div class="input-group mb-2">
        <input class="form-control" placeholder="Email" type="email" v-model="email" >
        <div > 
            <button type="submit" class="btn btn-warning">Enviar</button> 
        </div>
        
         </div>
             
        </form>
       </div>
   <!-- <div>
    <div class="input-group mb-2">
        <input type="text" class="form-control" placeholder="Email" >
        <div > 
            <button class="btn btn-warning">Enviar</button> 
        </div>
        
    </div>
    </div>-->
    <b-button class="btn-warning my-2 "  href="/">Pagina Principal</b-button>
  </div>
  <div id="Mision" class="Mision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Misión</b></h2>
  <p style="font-size:17px;"><b>KEAFMART</b> tiene como mision ser
   la tienda online proveedora de productos de alta calidad y precios accesibles a nivel regional.
    </p>
</div>

<div id="Vision" class="Vision">
  <h2 style="font-family:Dahnschrift Condensed"><b>Visión</b></h2>
  <p style="font-size:17px;">Convertirnos en la mayor dsitribuidora de productos de 
     las familias hondureñas. Siendo nuestro principal objetivo cuidar su bienestar ecónomico.</p>
</div>
<div style="width:100%;">    
   <div id="Contactanos" class="Contactanos"> 
      <h2 style="font-family:Dahnschrift Condensed"><b>Contáctanos</b></h2>  
      <br>
      <p style="font-size:17px;">
      ¡Para nosotros es un placer atenderle! <br> Contáctanos: 
      <a href="mailto:keafmart@gmail.com?Subject=Consulta">keafmart@gmail.com</a>
      <br>¡Gracias por su preferencia!
     </p> 
   <br><br>
  </div>
</div>
   
  </div>
</template>
<script>
import emailjs from 'emailjs-com';
    (function(){
        emailjs.init("user_wHZK23QE2EUwewsDI7iNW");
    })();
          
import {mapState,mapMutations, mapActions} from 'vuex';
export default {
        data(){
        return{
          enviaremail:false,
          email:'',
          Editar:{
              usuario:'',
              nombre:'',
              password:'',
              address : {
                  street :'',
                  city : '',
                  region : '',
                  postalCode :'',
                  country : '',
                  phone :''
             },
             ordenenPrevia:[] 
          },
          User:{
            street:'',
            city:'',
            Region:'',
            PostalCode:'',
            Country:''
          },
          ordenLista:false,
          ordenes:[],
          Nombre:"",
          TarjetaC:'',
           Completar:false,
           inform:{
               street:'',
               city:'',
               Region:'',
               PostalCode:'',
               Country:'',
               IDOrden:''
           },
           mensaje: {color: '', texto: ''},
           dismissSecs: 5,
           dismissCountDown: 0
        }
    },
    computed:{
      ...mapState(['estaCompra','total','orden','Carrito','Usuario'])      
    },
    created(){
      this.ObtenerOrdenes();
    },
    methods:{
        ...mapMutations(['Vaciar','AgregarAlCarrito','AgregarOrden','Aumentar','Disminuir','Modificar']),
        ObtenerOrdenes(){
          this.User=this.Usuario;
          this.inform.street=this.User.address.street;
          this.inform.city=this.User.address.city;
          this.inform.Region=this.User.address.region;
          this.inform.PostalCode=this.User.address.postalCode;
          this.inform.Country=this.User.address.country;
          this.Nombre=this.Usuario.nombre;
          this.axios.get('/ordenes') 
            .then(res=>{
             this.ordenes=res.data;
            
           })
          .catch(e=>{
              console.log(e.response);
           })

        },
        enviar:function(e){
            e.preventDefault();
            if(this.check()){
                var data = {
                   nombre: this.Nombre,
                   email: this.email,
                    OrderID: this.inform.IDOrden,
                };
                    emailjs.send("gmail","keafmart", data); 
                    this.email='';
                    this.enviaremail=false;
                    alert('Correo enviado con exito') 
               }
            else{
                 console.log('no hay correo');
            }
                
          },
          check:function(e) {
              if(!this.email)return false;
              else if(!this.validEmail(this.email)){return false;}
              else return true;
                 e.preventDefault();
          },
          validEmail:function(email) {
             var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(this.email);
          },

        AgregarCompras(){
          console.log('Veamos',this.Usuario)
            this.Editar.usuario=this.Usuario.usuario;
            this.Editar.nombre=this.Usuario.nombre;
            this.Editar.password=this.Usuario.password;
            this.Editar.address.street=this.Usuario.address.street; 
            this.Editar.address.city =this.Usuario.address.city;
            this.Editar.address.region =this.Usuario.address.region;
            this.Editar.address.postalCode=this.Usuario.address.postalCode;
            this.Editar.address.country=this.Usuario.address.country;
            this.Editar.address.phone=this.Usuario.address.phone;
            this.Editar.ordenenPrevia=this.Usuario.ordenenPrevia; 
         // console.log('Ia stoy arta',this.Editar)
         // console.log(this.Usuario._id,this.Usuario)
           if(this.Editar.nombre!=''){
              this.axios.put(`/cliente/${this.Usuario._id}`,this.Editar)
            .then(res=>{
              console.log(res.data)
            })
            .catch(e=>{
              console.log(e.response)
            })
           }
           
            
        },
        AgregarOrden(){
          
          console.log(this.User,'Importado',this.Usuario)
          var Ident=0;
          for(var item in this.ordenes){
            var user=parseInt(this.ordenes[item].orderID)
            
             if(user>Ident){
               Ident=user
               
             }
          }
          this.inform.IDOrden=(Ident+1).toString();
          
          this.$store.commit('Modificar',this.inform);
          var error="";
          if(this.TarjetaC==""){
            error="Tarjeta requerida"
            alert('Tarjeta de Crédito requerida');
            
          }else if(!this.validT(this.TarjetaC)){
              error="Tarjeta inválida"
              alert('Tarjeta Inválida')
              
          }
          if(!error.length){
            this.axios.post('/nueva-orden',this.$store.state.orden)
             .then(res=>{
                this.Completar=false;
                this.$store.commit('Vaciar');
                this.AgregarCompras();
                alert('¡Gracias por su preferencia '
                +this.Nombre+'! Su compra fue realizada con exito, Su ID de orden es: '
                +this.inform.IDOrden);
                 this.ordenLista=true;
                this.enviaremail=true;
              })
             .catch(e=>{
               console.log(e.response,'error');  
           })
          }
          
           
        },
        validT(TarjetaC){
           var re = /^(4[0-9]{3}-?[0-9]{4}-?[0-9]{4}-?[0-9]{4})|(5[1-5][0-9]{2}-?[0-9]{4}-?[0-9]{4}-?[0-9]{4})|((3|6)[1-5][0-9]{2}-?[0-9]{4}-?[0-9]{4}-?[0-9]{4})$/;
           console.log(re.test(TarjetaC))
           return re.test(TarjetaC);
        },
        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        }
         
    }

}
</script>

<style>
  .fondo{
  background-image: url("../assets/Fondo.jpg");
 }

  .Contactanos{
    border-radius: 15px 15px 15px 15px;
    -moz-border-radius: 15px 15px 15px 15px;
    -webkit-border-radius: 15px 15px 15px 15px;
    border: 0px solid #000000;
    text-align:center;
    background-color:#f8f9fa;
    margin-top:20px;
    margin-bottom:20px; 
    margin-left:26%; 
    margin-right:15px;
    padding: 15px;
    width:46%;
    height: 200px;
    
}
.Vision{
   border-radius: 15px 15px 15px 15px;
  -moz-border-radius: 15px 15px 15px 15px;
  -webkit-border-radius: 15px 15px 15px 15px;
   border: 0px solid #000000;
   background-color:#f8f9fa;
   margin-top:20px;
   margin-left:5px;
   margin-right:25px;
   display:inline-block;
   width:46%;
   padding: 15px;
}
.Mision{
   border-radius: 15px 15px 15px 15px;
   -moz-border-radius: 15px 15px 15px 15px;
   -webkit-border-radius: 15px 15px 15px 15px; 
   border: 0px solid #000000;
   margin-top:20px;
   margin-left:4%;
   display:inline-block;
   width:46%;
   padding: 15px;
   background-color:#f8f9fa;
}

</style>